// , , , , 
function f_get_mountains_of_the_region(sRegionName, objRegionInfo) {
    var aiMT_Idx = [];
    var i       = 0;
    var bNationalPark = 'Y';    //  .
    var asProvince = ['', '', '', '', '',
                      '', '', '', '', ''];

    if(sRegionName == '') {
        //      .
        for(i = 0; i < gdb_mt_master.length; i++) {
            if(gdb_mt_master[i].NationalPrak == 'Y') {
                aiMT_Idx.push(i);
            }
        }
        bNationalPark = 'N';    //    
    } 
     else if(asProvince.includes(sRegionName)) {
        //      .
        for(i = 0; i < gdb_mt_master.length; i++) {
            if(gdb_mt_master[i].Province == sRegionName) {
                aiMT_Idx.push(i);
            }
        }
     }
	/* 2023-02-07.    
     else if(sRegionName == '') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('', '', '');
     }
     else if(sRegionName == '') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('', '', '');
     }
     else if(sRegionName == '') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('', '', '_');
     }
	*/

    //      .
    // *****?????    .
    objRegionInfo.arrsMT_ID  = new Array();
    objRegionInfo.arriMT_Idx = new Array();
    for(i = 0; i < aiMT_Idx.length; i++) {
        if(gdb_mt_master[aiMT_Idx[i]].CometHiking == 'Y') {
            if(bNationalPark == 'Y' && gdb_mt_master[aiMT_Idx[i]].NationalPrak == 'Y')
                continue;   //   .         
            objRegionInfo.arrsMT_ID.push(gdb_mt_master[aiMT_Idx[i]].ID);
			// 2023-02-07,      --> new Array()  
			objRegionInfo.arriMT_Idx.push(aiMT_Idx[i]);
        }
    } 
    //     ,   .
    if(objRegionInfo.arrsMT_ID.length > 0)
        objRegionInfo.sRegionName = sRegionName;

    // *****????? .   .
    console.log(sRegionName + ": " + objRegionInfo.arrsMT_ID);
 
} // gf_get_mountains_of_the_region()             '', '', '', '', ''
 

// 2021-11-02,      .
//      ,        .
// input:
//      asRegions:   . f_get_mountains_of_the_region()  
//                      .
// output:
//     aobjRegionInfo:       
function gf_create_region_info(asRegions, aobjRegionsInfo) {
        //   
    let i = 0, k = 0;
    var iTotalHikingNo = 0;
    var dTotalDistance = 0;
 //   aobjRegionsInfo = new Array();      // ********?????????

    //    . look k:  
    for(k = 0; k < asRegions.length; k++) {
        var objRegionInfo = {};

        f_get_mountains_of_the_region(asRegions[k], objRegionInfo);
        console.log(objRegionInfo.arrsMT_ID);   /// ****

        objRegionInfo.arrobjDMTData = new Array();
        for(i = 0; i < objRegionInfo.arrsMT_ID.length; i++) {
            var objDMTData = {};
    
            f_create_one_mt_data(objRegionInfo.arrsMT_ID[i], objDMTData);

            console.log(objDMTData.iIndexOfHikings);
            
            //    
            if(objDMTData != null) {
                objRegionInfo.arrobjDMTData.push(objDMTData);
                iTotalHikingNo += objDMTData.iHikingNo;
                dTotalDistance += objDMTData.Distance;
            }
            ////////////// ****
            console.log('k: ' + k + 'i: ' + i);
            console.log("idxOfHikings:" + objRegionInfo.arrobjDMTData[i].iIndexOfHikings);
            console.log("hiking no:" + objRegionInfo.arrobjDMTData[i].iHikingNo);

        } // for i
        objRegionInfo.iTotalHikingNo  = iTotalHikingNo;
        objRegionInfo.dTotalDistance  = dTotalDistance;

        //  
        aobjRegionsInfo.push(objRegionInfo);
    }
    return;
}

//export default gf_create_one_mt_data
export default gf_create_region_info
