<!-- *************************
    
      // ***** Region Info: , , ,  *****
      objRegionInfo: {
        sRegionName: '...',
        arrsMT_ID: [],  	// db table key
		arriMT_Idx: [],		// db table , 2023-02-07
        arrsDMT:   [],  	// display 
        iMTNo:  2,
        iTotalHikingNo: 0,
        dTotalDistance: 0,
        arrobjDMTData: [],    //   
      },
      // ******    Display  *******
          objDMTData {   
              iHikingNo: 0,
              iIndexOfHikings: [],
              iRepresentativeNo,        // *****?????  
              iIdxOfRepresentative: [],
              iDisplayRowNo: 1,
              iDisplayHikingIdx: [],  //    ,   ,  ' 00  
              iDisplayRowSpan: 1,
              sSummary: '',
              iSummaryKey: 1,     // vue  .
              dTotalDistance: 0.0,
              iTotalTime: 0,
          },
-->

<template>
  <div>
    <!-- Region   for  . , , ?, , 
          : RInfo = Region Info
          : mt1   = region   
          : hiking_idx = gdb_mt_comet_hiking[]  
    -->
    
    <div v-for="(RInfo, i) in aobjRegionsInfo" v-bind:key="i">
      <!------------ Region   ----------------       {{RInfo.arrobjDMTData[i].iIndexOfHikings}}-->
      <h3>{{i}}: {{RInfo.sRegionName}}</h3>
      {{RInfo.arrsMT_ID}}
      <!------------ table   ------------------>
      <table class="mt_trip_table">
        <!-- template Region     for  . -->
		<!--  , ,   ,     ...-->
        <template v-for="(mt1, j) in RInfo.arrobjDMTData">
            <tr class="mt_trip_row" :key="mt1.iSummaryKey">
				<td class="mt_trip_col1">{{RInfo.arrsMT_ID[j]}}</td>
				<td class="mt_trip_col2"> {{gdb_mt_master[RInfo.arriMT_Idx[j]].Height_Meter}}  m</td>
				<td class="mt_trip_col_by100">
					<template v-if="gdb_mt_master[RInfo.arriMT_Idx[j]].Blackyak100 == 'Y'">	BY 100
					</template>
				</td>
				<td class="mt_trip_col3">{{mt1.iHikingNo}}</td>
            </tr>

		
        <!--     3  . 
		**** 2023-02-07,       *****
          <tr v-for="(hiking_idx, k) in mt1.iDisplayHikingIdx" :key="hiking_idx" 
              class="mt_trip_row">
            <td v-if="k === 0" :rowspan=mt1.iDisplayRowSpan
                class="mt_trip_col1"
            >
                  {{RInfo.arrsMT_ID[j]}}
            </td>
            <td class="mt_trip_col2">
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(0,4)}}<br>
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(5,7)}}
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(8,10)}}
            </td>
            <td class="mt_trip_col3">
              {{gdb_mt_comet_hiking[hiking_idx].Path}}
              <br>
              {{gdb_mt_comet_hiking[hiking_idx].Distance}}km,
              {{gdb_mt_comet_hiking[hiking_idx].HikingTime2}},
            </td>
          </tr>
          <template v-if="mt1.iSummaryKey != -1">
            <tr class="mt_trip_row" :key="mt1.iSummaryKey"
            >
              <td class="mt_trip_col2"></td>
              <td class="mt_trip_col3">{{mt1.sSummary}}</td>
            </tr>
          </template>
           -->
		
		</template>
      </table>
    </div>
  </div>
</template>

<script>
  import gdb_mt_master          from '../database/db_mt_master'
  import gf_create_region_info  from '../database/gf_create_one_mt_data'
  import gdb_mt_comet_hiking    from '../database/db_mt_comet_hiking'
  
  export default {
    mounted: function() {
      //        .   '', '', ''
      gf_create_region_info(this.asRegions, this.aobjRegionsInfo);
    },
    data () {
      return {
        aobjRegionsInfo: [],
        asRegions: [
          '', '', '', '', '', '', '', '',
          '', '', ''
        ],
        gdb_mt_master,
        gdb_mt_comet_hiking,
  }
    }
  }
</script>

<!-- () -->
<style scoped>
  .mt_trip_table {
    display: table;
    width: 100%;
    border-collapse: collapse;
    border-color: rgba(170, 170, 170, .6)
  }

  .mt_trip_row {
    display: table-row;
    width: 100%;
    border-collapse: collapse;
    border-color: rgba(170, 170, 170, .6)
    }

  .mt_trip_col_by100 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: center;
    padding: 4px 2px 4px 2px;
    }

  .mt_trip_col1 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: center;
    padding: 4px 2px 4px 2px;
    }

  .mt_trip_col2 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: right;
    padding: 4px 4px 4px 4px;
  }  
  .mt_trip_col3 {
    display: table-cell;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    padding: 4px 4px 4px 4px;
  }  
</style>